input[type='text'],
input[type='tel'],
input[type='email'],
input[type='search'],
input[type='password'],
input[type='url'],
textarea {
	@include placeholder {
		color: lighten($gray-light, 15%);
	};

	display: block;
	width: 100%;
	-webkit-appearance: none;
	-webkit-border-radius: 0;
	padding: 10px 15px;
	font-size: 18px;
	line-height: 1.5;
	font-family: $base-font-family;
	border: none;
	border-bottom: 2px solid $gray-light;
	background-color: transparent;
	transition: border-color 0.3s, background-color 0.3s;

	&:hover {
		background-color: rgba($gray, 0.05);
	}

	&:not(textarea) {
		height: 50px;
	}

	&:focus {
		outline: none;
		border-color: $primary;
	}

	&.active {
		border-color: $primary;
	}
}

textarea {
	height: 175px;
	resize: none;
}

button,
input[type='button'],
input[type='reset'],
input[type='file'],
input[type='submit'] {
	outline: none;
	cursor: pointer;
}

.checkbox-radio-list {
	@include listreset;

	&__item {
		&:not(:last-child) {
			margin-bottom: 15px;
		}
	}
}

.checkbox-radio-question {
	display: block;
	max-width: 350px;

	&__input {
		&:checked {
			+ .checkbox-radio-question__item {
				background-color: $primary;

				&:after {
					opacity: 1;
				}
			}
		}
	}

	&__item {
		position: relative;
		display: block;
		background-color: $gray-light;
		color: $white;
		padding: 15px 15px 15px 60px;
		cursor: pointer;
		border-radius: 2px;
		transition: background-color 0.3s;

		&:hover {
			background-color: darken($gray-light, 10%);
		}

		&:after {
			content: url("../img/check-icon.svg");
			display: inline-block;
			width: 20px;
			position: absolute;
			top: 50%;
			right: 15px;
			transform: translate(0, -50%);
			opacity: 0;
			transition: opacity 0.3s;
		}
	}

	&__number-box {
		@include inlineFlexCenter;
		@include sizeElement(40px);

		position: absolute;
		top: 50%;
		left: 10px;
		transform: translate(0, -50%);
		border: 1px solid $white;
		padding: 10px;
		border-radius: 2px;
		font-size: 22px;
	}

	&__text {
		font-size: 18px;
		font-weight: 500;
	}
}

.survey-form {
	&__submit-block {
		padding-top: 80px;
	}

	&__submit-text {
		margin-bottom: 30px;
	}
}