.rating {
	@include listreset;

	display: inline-flex;
	font-size: 40px;
	letter-spacing: 3px;
	color: $gray-light;

	&__item {
		cursor: pointer;
		display: inline-flex;
	}

	&.hovering {
		&[data-hover="1"] {
			.rating__item {
				&:nth-child(-n + 1) {
					color: $yellow;
				}
			}
		}

		&[data-hover="2"] {
			.rating__item {
				&:nth-child(-n + 2) {
					color: $yellow;
				}
			}
		}

		&[data-hover="3"] {
			.rating__item {
				&:nth-child(-n + 3) {
					color: $yellow;
				}
			}
		}

		&[data-hover="4"] {
			.rating__item {
				&:nth-child(-n + 4) {
					color: $yellow;
				}
			}
		}

		&[data-hover="5"] {
			.rating__item {
				&:nth-child(-n + 5) {
					color: $yellow;
				}
			}
		}
	}

	&:not(.hovering) {
		&[data-select="1"] {
			.rating__item {
				&:nth-child(-n + 1) {
					color: $primary;
				}
			}
		}

		&[data-select="2"] {
			.rating__item {
				&:nth-child(-n + 2) {
					color: $primary;
				}
			}
		}

		&[data-select="3"] {
			.rating__item {
				&:nth-child(-n + 3) {
					color: $primary;
				}
			}
		}

		&[data-select="4"] {
			.rating__item {
				&:nth-child(-n + 4) {
					color: $primary;
				}
			}
		}

		&[data-select="5"] {
			.rating__item {
				&:nth-child(-n + 5) {
					color: $primary;
				}
			}
		}
	}
}