.section {
	padding: 50px 0;

	@include min-w(md) {
		padding: 70px 0;
	}

	@include min-w(lg) {
		padding: 95px 0;
	}

	&-starting {
		min-height: calc(100vh - (#{$header-height} + #{$header-height}));
		min-height: calc((var(--vh, 1vh) * 100) - (#{$header-height} + #{$header-height}));
		transition: min-height 0.5s;
		display: flex;
		align-items: center;

		&__logo {
			text-align: center;
			max-width: 170px;
			margin: 0 auto 50px;

			@include min-w(sm) {
				max-width: 100%;
			}

			@include min-w(lg) {
				margin-bottom: 70px;
			}
		}

		&__text {
			@include min-w(md) {
				font-size: 18px;
			}
		}

		&__btn-block {
			padding-top: 50px;
			text-align: center;

			@include min-w(sm) {
				padding-top: 70px;
			}
		}
	}

	&-survey {
		padding-top: 10px;
		padding-bottom: 200px;

		@include min-w(md) {
			padding-top: 30px;
		}

		@include min-w(lg) {
			padding-top: 55px;
		}
	}

	&-confirmation {
		min-height: calc(100vh - #{$header-height});
		min-height: calc((var(--vh, 1vh) * 100) - #{$header-height});
		transition: min-height 0.5s;
		display: none;
		align-items: center;

		&.active {
			display: flex;
		}

		&__logo {
			text-align: center;
			max-width: 170px;
			margin: 0 auto 50px;

			@include min-w(sm) {
				max-width: 100%;
			}

			@include min-w(lg) {
				margin-bottom: 70px;
			}
		}

		&__img {
			text-align: center;
		}

		&__btn-block {
			padding-top: 50px;
			text-align: center;

			@include min-w(sm) {
				padding-top: 70px;
			}
		}
	}
}