.survey-question-item {
	opacity: 0.4;
	pointer-events: none;
	transition: opacity 0.3s;

	&.active {
		opacity: 1;
		pointer-events: auto;
	}

	&.valid.focused {
		.survey-question-item__next-btn {
			display: block;
		}
	}

	&.not-valid {
		.error-text {
			display: block;
		}
	}

	&__question {
		font-weight: 500;
		cursor: pointer;

		@include min-w(md) {
			margin-bottom: 40px;
		}
	}

	&__next-btn {
		display: none;
		padding-top: 20px;
	}
}