.preloader {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 100%;
	height: 100%;
	z-index: $zindex-preloader;
	background-color: rgba($white, 0.96);
	opacity: 0;
	visibility: hidden;
	transition: opacity 0.3s, visibility 0.3s;

	&.active {
		opacity: 1;
		visibility: visible;
	}

	&__item {
		@include absoluteCenter;
	}

	&__circles-block {
		display: flex;
		height: 65px;
	}

	&__circle {
		@include sizeElement(15px);

		margin: 0 2px;
		display: inline-block;
		background-color: $primary;
		border-radius: 50%;
		animation: loading 1.5s cubic-bezier(.8, .5, .2, 1.4) infinite;
		transform-origin: bottom center;

		&:nth-child(1) {
			animation-delay: 0.1s;
		}
		&:nth-child(2) {
			animation-delay: 0.2s;
		}
		&:nth-child(3) {
			animation-delay: 0.3s;
		}
		&:nth-child(4) {
			animation-delay: 0.4s;
		}
		&:nth-child(5) {
			animation-delay: 0.5s;
		}
		&:nth-child(6) {
			animation-delay: 0.6s;
		}
		&:nth-child(7) {
			animation-delay: 0.7s;
		}
		&:nth-child(8) {
			animation-delay: 0.8s;
		}
	}

	@keyframes loading {
		0% {
			transform: translateY(0px);
			opacity: 1;
		}
		50% {
			transform: translateY(50px);
			opacity: 0;
		}
		100% {
			transform: translateY(0px);
			opacity: 1;
		}
	}

	&__text {
		text-align: center;
		text-transform: uppercase;
		font-weight: 500;
		padding-top: 50px;
		letter-spacing: 1px;
	}
}