.btn {
	display: inline-flex;
	justify-content: center;
	text-align: center;
	border-radius: 4px;
	text-decoration: none;
	font-weight: 500;
	border-width: 1px;
	line-height: 1;
	border-style: solid;
	transition: border-color 0.3s, background-color 0.3s, color 0.3s;

	&-primary {
		background-color: $primary;
		border-color: darken($primary, 10%);
		color: $white;

		&:focus {
			outline: 5px solid rgba($primary, 0.25);
		}

		&:hover {
			background-color: darken($primary, 10%);
			color: $white;
		}
	}

	&.with-pulsing {
		box-shadow: 0 0 0 0 rgba($primary, 0.8);
		animation: pulsing 1.25s infinite cubic-bezier(0.66, 0, 0, 1);
	}

	&:not(.btn-sm):not(.btn-lg) {
		font-size: 18px;
		min-width: 150px;
		padding: 12px 15px;
	}
}

@keyframes pulsing {
	to {
		box-shadow: 0 0 0 20px rgba($primary, 0);
	}
}