@import "variables";

@font-face {
  font-family: '#{$icomoon-font-family}';
  src:  url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?y8z5kw');
  src:  url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?y8z5kw#iefix') format('embedded-opentype'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?y8z5kw') format('truetype'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.woff?y8z5kw') format('woff'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.svg?y8z5kw##{$icomoon-font-family}') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icomoon-"], [class*=" icomoon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: '#{$icomoon-font-family}' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icomoon-star {
  &:before {
    content: $icomoon-star; 
  }
}

