// COLORS =================================

$white:         #ffffff !default;
$green:         #6fc957 !default;
$red:           #db3f3f !default;
$yellow:        #f8a400 !default;
$gray:          #4b4b4b !default;
$gray-light:    #8d8d8d !default;
$black:         #000000 !default;

$primary:       $green !default;
$secondary:     $gray !default;

// BASE =================================
$max-width-center-container:  770px !default;
$gutter:                      15px !default;
$padding-container:           0 $gutter !default;

$base-background-body:        $white !default;
$base-min-width:              320px !default;

$header-height:               60px !default;

$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px
) !default;

// TYPOGRAPHY =================================
$base-font-sans-serif:        "Rubik", "Arial", "Helvetica Neue", "Helvetica", sans-serif !default;
$base-font-serif:             "Times New Roman", "Times", "Baskerville", "Georgia", serif !default;

$base-font-family:            $base-font-sans-serif !default;
$base-text-color:             $secondary !default;
$font-size-base:              16px !default;
$line-height-base:            1.3 !default;

$headings-font-family:        inherit !default;
$headings-color:              inherit !default;

$base-link-color:             $secondary !default;
$base-link-hover-color:       $primary !default;
$text-decoration:             underline !default;
$text-decoration-hover:       none !default;

// Z-INDEX MASTER LIST =================================
$zindex-dropdown:             1000 !default;
$zindex-sticky:               1020 !default;
$zindex-fixed:                1030 !default;
$zindex-offcanvas-backdrop:   1040 !default;
$zindex-offcanvas:            1045 !default;
$zindex-modal-backdrop:       1050 !default;
$zindex-modal:                1055 !default;
$zindex-popover:              1070 !default;
$zindex-tooltip:              1080 !default;
$zindex-preloader:            1090 !default;

// ICOMOON FONT PATH =================================
$icomoon-font-path: "../fonts/icomoon";