.text-block {
	p:last-child {
		margin-bottom: 0;
	}
}

p {
	margin: 0 0 15px;
}

strong {
	font-weight: 500;
}

small {
	font-size: 14px;
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
	font-family: $headings-font-family;
	color: $headings-color;
	font-weight: 400;
	margin-top: 0;
	margin-bottom: 20px;
}

h1, .h1 {
	font-size: 28px;

	@include min-w(md) {
		font-size: 32px;
	}
}

h2, .h2 {
	font-size: 24px;

	@include min-w(md) {
		font-size: 26px;
	}

	@include min-w(lg) {
		font-size: 28px;
	}
}

h3, .h3 {
	font-size: 26px;
}

h4, .h4 {
	font-size: 24px;
}

h5, .h5 {
	font-size: 22px;
}

h6, .h6 {
	font-size: 20px;
	margin-bottom: 10px;
}

.error-text {
	display: none;
	margin-bottom: 0;
	font-size: 18px;
	padding-top: 10px;
	color: $red;

	&.active {
		display: block;
	}
}