.header {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: $zindex-fixed;
	display: flex;
	align-items: center;
	min-height: $header-height;
	background-color: $base-background-body;
	border-bottom: 2px solid rgba($secondary, 0.2);
	padding: 10px 0;

	&__logo {
		display: flex;
		max-width: 115px;
	}
}