.survey-bottom-bar {
	position: fixed;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: $zindex-fixed;
	display: flex;
	align-items: center;
	min-height: $header-height;
	background-color: $base-background-body;
	border-top: 2px solid rgba($secondary, 0.2);
	padding: 10px 0;

	&:before {
		content: '';
		position: absolute;
		bottom: calc(100% + 2px);
		left: 0;
		right: 0;
		z-index: 1;
		display: block;
		height: 80px;
		width: 100%;
		pointer-events: none;
		background: linear-gradient(to top, $white, rgba($white, 0));
	}

	&__completed-line {
		position: absolute;
		bottom: 100%;
		left: 0;
		z-index: 5;
		height: 2px;
		width: 0%;
		background-color: $primary;
		transition: width 0.5s, opacity 0.5s;

		&[data-completed="0"] {
			opacity: 0;
		}

		&:before {
			content: attr(data-completed) "%";
			display: inline-block;
			font-size: 14px;
			line-height: 1;
			font-weight: 700;
			position: absolute;
			bottom: 5px;
			right: 0;
		}
	}

	&__counter {
		margin-bottom: 0;
		font-size: 20px;
	}
}