.vertical-text-top-bottom {
	writing-mode: vertical-rl;
	-webkit-writing-mode: vertical-rl;
	display: inline-block;
	margin: 0;
}

.vertical-text-bottom-top {
	writing-mode: vertical-rl;
	-webkit-writing-mode: vertical-rl;
	transform: rotate(180deg);
	display: inline-block;
	margin: 0;
}

.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
	border: 0;
	clip: rect(0 0 0 0);
	overflow: hidden;
}

.overflow-hidden {
	overflow: hidden;
}

.text-overflow {
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.ofi {
	@include absoluteCover;

	object-fit: cover;
}

.text-primary {
	color: $primary !important;
}

.hide {
	display: none !important;
}